<template>
  <div>
    <div class="todo-list">
    <md-card>
      <md-card-header :data-background-color="dataBackgroundColor">
        <h4 class="title">Hallo {{ showUser }}</h4>
      </md-card-header>
      <md-card-content>
        <input type="text" class="todo-input" placeholder="Was muss ich machen" v-model="newTodo"
               @keyup.enter="addTodo">
        <transition-group name="fade" enter-active-class="animated fadeInUp"
                          leave-active-class="animated fadeOutDown">
          <todo-item v-for="todo in todosFiltered" :key="todo.id" :todo="todo" :checkAll="!anyRemaining">
          </todo-item>
        </transition-group>

        <div class="extra-container">
          <TodoCheckAll/>
<!--          <TodoClearCompleted/>-->
          <TodoItemsRemaining/>
        </div> <!-- end extra-container -->

        <div class="extra-container" style="justify-content: center">
          <todo-filtered></todo-filtered>
        </div> <!-- end extra-container -->
      </md-card-content>
    </md-card>
  </div>
  </div>
</template>

<script>
import axios from "axios";
import TodoItem from './TodoItem'
import TodoItemsRemaining from './TodoItemsRemaining'
import TodoCheckAll from './TodoCheckAll'
import TodoFiltered from './TodoFiltered'
// import TodoClearCompleted from './TodoClearCompleted'

export default {
  name: 'todo-list',
  components: {
    TodoItem,
    TodoItemsRemaining,
    TodoCheckAll,
    TodoFiltered,
    // TodoClearCompleted,
  },
  props: {
    dataBackgroundColor: {
      type: String,
      default: "green"
    }
  },
  data() {
    return {
      newTodo: '',
    }
  },
  created() {
    this.fetchData()
    this.$store.dispatch('retrieveUserName')
  },

  computed: {
    anyRemaining() {
      return this.$store.getters.anyRemaining
    },
    todosFiltered() {
      return this.$store.getters.todosFiltered
    },
    showUser() {
      return this.$store.getters.showUser;
    }
  },
  methods: {
    fetchData() {
      this.$store.dispatch('retrieveTodos')
    },
    addTodo() {
      axios.post("/item/store", {
        item: {
          "name": this.newTodo
        }
      }).then(this.fetchData()).then(this.newTodo = '');
    },
  }
}
</script>

