<template>
  <div class="todo-list">
    <md-card>
      <md-card-header :data-background-color="dataBackgroundColor">
        <h4 class="title" style="text-align: center">{{ this.groupName }}</h4>
      </md-card-header>
      <md-card-content>
        <input type="text" class="todo-input" placeholder="What needs to be done" v-model="newGroupTodo"
               @keyup.enter="addGroupTodo">
        <transition-group name="fade" enter-active-class="animated fadeInUp" leave-active-class="animated fadeOutDown">
          <group-todo-item v-for="groupTodo in groupTodosFiltered" :key="groupTodo.id" :todo="groupTodo" :checkAll="!anyGroupRemaining">
<!--                           :importantGroupTodo="groupTodo.important">-->
          </group-todo-item>
        </transition-group>

        <div class="extra-container">
          <GroupTodoCheckAll/>
<!--          <GroupTodoClearCompleted/>-->
          <GroupTodoItemsRemaining/>
        </div> <!-- end extra-container -->

        <div class="extra-container" style="justify-content: center">
          <GroupTodoFiltered/>
        </div> <!-- end extra-container -->
      </md-card-content>
    </md-card>
  </div>
</template>

<script>
import GroupTodoItemsRemaining from '@/components/groupTodos/GroupTodoItemsRemaining'
import GroupTodoCheckAll from '@/components/groupTodos/GroupTodoCheckAll'
import GroupTodoFiltered from '@/components/groupTodos/GroupTodoFiltered'
// import TodoClearCompleted from '@/components/todos/TodoClearCompleted'
import axios from "axios";
import GroupTodoItem from "@/components/groupTodos/GroupTodoItem";

export default {
  name: 'Group-todos',
  components: {
    GroupTodoItem,
    GroupTodoItemsRemaining,
    GroupTodoCheckAll,
    GroupTodoFiltered,
    // TodoClearCompleted,
  },
  data() {
    return {
      groupId: 0,
      newGroupTodo: '',
      groupName:'',
    }
  },
  props: {
    dataBackgroundColor: {
      type: String,
      default: "green"
    }
  },
  computed: {
    groupTodosFiltered() {
      return this.$store.getters.groupTodosFiltered
    },
    groups() {
      return this.$store.state.groups
    },
    showGroupName() {
      return this.$store.getters.showGroupName;
    },
    anyGroupRemaining() {
      return this.$store.getters.anyGroupRemaining
    },
  },
  methods: {
    findGroupName(){
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
      axios.get('/Allgroups').then(response => {
        response.data.forEach(group => {
          if(group.id == this.groupId){
            this.groupName = group.name
          }
        })
      })
      // return this.$store.state.groups.forEach(group => {
      //   if(group.id == this.groupId){
      //     this.groupName = group.name
      //   }
      // })
    },
    addGroupTodo() {
      axios.post('/group/' + this.groupId + '/store', {
        item: {
          "name": this.newGroupTodo
        }
      }).then(this.newGroupTodo = '').then(this.$store.dispatch('retrieveGroupTodos', this.groupId));
    },
  },
  created() {
    this.$store.dispatch('getAssigneeName')
    this.groupId = this.$route.params.id
    this.$store.dispatch('retrieveGroupTodos', this.groupId)
    this.$store.dispatch('retrieveGroups', this.groupId)
    this.findGroupName()

  },
}
</script>