<template>
  <div>
    <md-dialog :md-active.sync="showDialog" >
      <div style="padding: 45px;margin: auto;width: 700px;height: 80%">
        <md-dialog-title style="display: flex; justify-content: space-between">
          <div style="font-size: 30px">
            {{title }}
          </div>
          <div>
            <b-icon v-if="(this.important == 0)" icon="star" font-scale="1.5" class="important"
                    @click="importantToggle()" style="margin-right: 10px"></b-icon>
            <b-icon v-else-if="(this.important == 1)" icon="star-fill" font-scale="1.5" class="important"
                    @click="importantToggle()" style="margin-right: 10px"></b-icon>
            <b-icon icon="trash" font-scale="1.5" @click="removeTodo(todo.id)" style="margin-right: 10px"></b-icon>
          </div>
        </md-dialog-title>

        <md-field>
          <label>Change title</label>
          <md-input v-model="title" @blur="doneEdit" @keyup.enter="doneEdit" @keyup.esc="cancelEdit" >>
          </md-input>
        </md-field>

        <md-field>
          <label>Beschreibung</label>
          <md-textarea v-model="description" @keydown.enter.prevent="doneEdit" @blur="doneEdit">
            {{ description }}
          </md-textarea>
        </md-field>
        <md-dialog-actions>
          <md-button class="md-primary" @click="showDialog = false">Close</md-button>
          <md-button class="md-secondary" @click="fetchData(id)">Save</md-button>
        </md-dialog-actions>
      </div>
    </md-dialog>  <!----  end of a dialog-->


    <button @click="showDialog = true">
      <b-icon-pencil font-scale="1.5"></b-icon-pencil>
    </button>

  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'todo-modal',
  props: {
    todo: {
      type: Object,
      required: true,
    },
      importantTodo: {
        // type: Boolean,
        // required: true,
      },
  },
  data() {
    return {
      'id': this.todo.id,
      'title': this.todo.name,
      'completed': this.todo.completed,
      'editing': this.todo.editing,
      'important': this.todo.important,
      'description': this.todo.description,
      'beforeEditCache': '',
      assignee: null,
      groupId: 0,
      users: [],
      assignees: [],
      showDialog: false,
      options: []
    }
  },
  computed: {
    showAssign() {
      return this.$store.getters.showAssign
    },
  },
  watch: {
    importantTodo(){
      this.important = this.importantTodo?true:false
    },
  },
  methods: {
    fetchData() {
      this.$store.state.todos  = []
      this.$store.dispatch('retrieveTodos')

      // this.$store.dispatch('retrieveTodo', id)
    },
    removeTodo(id) {
      this.$store.dispatch('deleteTodo', id)
    },
    editTodo() {
      this.beforeEditCache = this.title
      this.editing = true
    },
    doneEdit() {
      if (this.title.trim() == '') {
        this.title = this.beforeEditCache
      }
      this.editing = false
      this.$store.dispatch('updateTodo', {
        'id': this.id,
        'title': this.title,
        'completed': this.completed,
        'editing': this.editing,
        'important': this.important,
        'description':this.description,
      })
    },
    cancelEdit() {
      this.title = this.beforeEditCache
      this.editing = false
    },
    importantToggle() {
      console.log(this.important)
      console.log(this.importantTodo)

      if(this.important == 0 || null) {
        this.important = true
      } else {
        this.important = false
      }
      axios.put('/item/' + this.id, {
        item: {
          "name": this.title,
          "important": this.important,
          "completed":this.todo.completed,
          'description':this.todo.description,

        }
      })
    },
  },
}
</script>