<template>
  <div>
    <div>
      <b-button style="margin-right: 10px" :class="{ active: groupFilter == 'all' }" @click="changeGroupFilter('all')" pill
                variant="outline-secondary">Alle
      </b-button>
      <b-button style="margin-right: 10px" :class="{ active: groupFilter == 'active' }" @click="changeGroupFilter('active')" pill
                variant="outline-secondary">Aktiv
      </b-button>
      <b-button style="margin-right: 10px" :class="{ active: groupFilter == 'completed' }" @click="changeGroupFilter('completed')"
                pill
                variant="outline-secondary">Fertig
      </b-button>
      <b-button :class="{ active: groupFilter == 'important' }" @click="changeGroupFilter('important')" pill
                variant="outline-secondary">Wichtig
      </b-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'group-todo-filtered',
  computed: {
    groupFilter() {
      return this.$store.state.groupFilter
    }
  },
  methods: {
    changeGroupFilter(groupFilter) {
      this.groupId = this.$route.params.id
      this.$store.dispatch('updateGroupFilter', groupFilter)
      this.$store.dispatch('retrieveGroupTodos',this.groupId)
    }
  }
}
</script>