<template>
  <div>
    <md-dialog :md-active.sync="showDialog">
      <div style="padding: 45px;margin: auto;width: 700px;height: 90%">
        <md-dialog-title style="display: flex; justify-content: space-between">
          <div style="font-size: 30px">
            {{ title }}
          </div>
          <div>
            <b-icon v-if="(this.important == 0)" icon="star" font-scale="1.5" class="important"
                    @click="importantToggle()" style="margin-right: 10px"></b-icon>
            <b-icon v-else-if="(this.important == 1)" icon="star-fill" font-scale="1.5" class="important"
                    @click="importantToggle()" style="margin-right: 10px"></b-icon>
            <b-icon icon="trash" font-scale="1.5" @click="removeGroupTodo(todo.id)" style="margin-right: 10px"></b-icon>
          </div>
        </md-dialog-title>

        <md-field>
          <label>Change title</label>
          <md-input v-model="title" @blur="doneEditGroupTodo" @keyup.enter="doneEditGroupTodo" @keyup.esc="cancelEdit">>
          </md-input>
        </md-field>

        <md-field>
          <label>Beschreibung</label>
          <md-textarea v-model="description" @keydown.enter.prevent="doneEditGroupTodo" @blur="doneEditGroupTodo">
            {{ description }}
          </md-textarea>
        </md-field>

        <span @click="showAssignForm">
            <md-icon class="fa fa-plus"></md-icon>
              </span>

        <h4 v-if="showAssign">Add assignees</h4>
        <v-select @search="fetchGroupMembers" :options="options" label="name" v-model="assignee"
                  v-if="showAssign">

          <template slot="option" slot-scope="option">
            <md-avatar style="margin-right: 5px">
              <img :src="`https://todolist-oncymstoua-ey.a.run.app${option.file_path}`" alt="avatar"/>
            </md-avatar>
            {{ option.name }}
          </template>

          <template slot="selected-option" slot-scope="option">
            <md-avatar style="margin-right: 5px">
              <img :src="`https://todolist-oncymstoua-ey.a.run.app${option.file_path}`" alt="avatar"/>
            </md-avatar>
            {{ option.name }}
          </template>

          <!--        <template v-slot:spinner="{ loading }">-->
          <!--          <div v-show="loading">Loading...</div>-->
          <!--        </template>-->

        </v-select>

        <div style="display: flex;justify-content: space-between">
          <label>Users</label>
        </div>
        <md-avatar v-for="user in todo.assignees " :key="user.id" style="margin-right: 10px;z-index: 10000"
                   md-menu-trigger>
          <!--        <img src="@/assets/avatar.jpg" alt="Avatar" @click="deleteAssignee(todo.id, user.id)">-->
          <img class="img" :src="`https://todolist-oncymstoua-ey.a.run.app${user.file_path}`"
               @click="deleteAssignee(todo.id, user.id)"/>

          <md-tooltip md-direction="bottom">{{ user.name }}</md-tooltip>
        </md-avatar>
        <div>
          <md-button class="md-secondary" @click="assign(id,assignee.id)">Add User</md-button>
        </div>

        <md-dialog-actions>
          <md-button class="md-primary" @click="showDialog = false">Close</md-button>
          <md-button class="md-secondary" @click="updateData()">Save</md-button>

        </md-dialog-actions>
      </div>
    </md-dialog>  <!----  end of a dialog-->


    <button @click="showDialog = true">
      <b-icon-pencil font-scale="1.5"></b-icon-pencil>
    </button>

  </div>
</template>

<script>
import axios from "axios";

export default {
  props: {
    todo: {
      type: Object,
      required: true,
    },
    importantGroupTodo: {
      // type: Boolean,
    },
  },
  data() {
    return {
      'id': this.todo.id,
      'title': this.todo.name,
      'completed': this.todo.completed,
      'editing': this.todo.editing,
      'important': this.todo.important,
      'description': this.todo.description,
      'beforeEditCache': '',
      assignee: null,
      groupId: 0,
      users: [],
      assignees: [],
      showDialog: false,
      options: []
    }
  },
  watch: {
    importantGroupTodo() {
      this.important = this.importantGroupTodo ? true : this.todo.important
    },
  },
  computed: {
    showAssign() {
      return this.$store.getters.showAssign
    },
  },
  methods: {
    fetchGroupMembers(search, loading) {
      if (search.length == 5) {
        loading(true);
        axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
        axios.get('/group/' + this.groupId + '/members')
            .then(response => {
              console.log(response.data)
              this.options = response.data
            }).then(loading(false))
            .catch(error => {
              console.log(error)
            })
      }
    },

    showAssignForm() {
      this.$store.dispatch('showAssign')
    },

    assign(id, userId) {
      axios.put('/group/item/assignee/' + id + '/' + userId)
          .then(this.fetchData)
    },
    deleteAssignee(id, userId) {
      axios.delete('/group/item/assignee/' + id + '/' + userId)
          .then(this.fetchData)
    },
    fetchData() {
      this.groupId = this.$route.params.id
      this.$store.dispatch('retrieveGroupTodos', this.groupId)
    },
    updateData() {
      this.$store.state.groupTodos = []
      this.$store.dispatch('retrieveGroupTodos', this.groupId)

    },
    removeGroupTodo(id) {
      this.$store.dispatch('deleteGroupTodo', id)
    },
    editTodo() {
      this.beforeEditCache = this.title
      this.editing = true
    },
    doneEditGroupTodo() {
      if (this.title.trim() == '') {
        this.title = this.beforeEditCache
      }
      this.editing = false
      this.$store.dispatch('updateGroupTodo', {
        'id': this.id,
        'title': this.title,
        'completed': this.completed,
        'editing': this.editing,
        'important': this.important,
        'description': this.description
      })
    },
    cancelEdit() {
      this.title = this.beforeEditCache
      this.editing = false
    },
    importantToggle() {
      if (this.important == 0 || null) {
        this.important = true
      } else {
        this.important = false
      }
      axios.put('group/item/' + this.id, {
        item: {
          "name": this.title,
          "important": this.important,
          "completed": this.todo.completed,
          'description': this.todo.description,
        }
      })
    },
  },

  created() {
    this.groupId = this.$route.params.id
    // this.findGroupUsers()
  },
}
</script>
