<template>
  <md-card class="md-card-profile">
    <md-card-content>
      <h4>My todo</h4>
      <md-progress-bar md-mode="buffer" :md-value="this.todosFiltered.filter(todo=>todo.completed).length * 100 /this.todosFiltered.length"
                       :md-buffer="100"></md-progress-bar>
    </md-card-content>
  </md-card>
</template>

<script>
export default {
  name: "user-groups",
  data() {
    return {};
  },
  computed: {
    todosFiltered() {
      return this.$store.getters.todosFiltered
    },
  },
  created() {
    this.$store.dispatch('retrieveTodos')
  },
};
</script>
