<template>
  <div class="todo-item">
    <div class="todo-item-left">
      <input type="checkbox" v-model="completed" @change="doneEdit">
      <!--      <md-checkbox   v-model="completed"  @change="doneEdit"></md-checkbox>-->
      <div v-if="!editing" @dblclick="editTodo" class="todo-item-label" :class="{ completed : completed }"
           style="font-size: 20px">{{ title }}
      </div>
      <input v-else class="todo-item-edit" type="text" v-model="title"
             @blur="doneEdit" @keyup.enter="doneEdit" @keyup.esc="cancelEdit" v-focus>
    </div> <!-- end todo-item-left -->

    <div class="remove-item">
      <b-icon v-if="!important || important == undefined " icon="star" font-scale="1.5" class="important"
              @click="importantToggle()" style="margin-right: 10px"></b-icon>
      <b-icon v-else-if="important || important == undefined" icon="star-fill" font-scale="1.5" class="important"
              @click="importantToggle()" style="margin-right: 10px"></b-icon>
      <b-icon icon="trash" font-scale="1.5" @click="removeTodo(todo.id)" style="margin-right: 10px"></b-icon>
      <TodoModal :todo="todo" :importantTodo="important"/>
    </div>
  </div> <!-- end todo-item -->
</template>

<script>
import axios from "axios";
import TodoModal from "@/components/todos/TodoModal";

export default {
  name: 'todo-item',
  components: {TodoModal},
  props: {
    todo: {
      type: Object,
      required: true,
    },
    checkAll: {
      type: Boolean,
      required: true,
    },
  },
  data() {
    return {
      'id': this.todo.id,
      'title': this.todo.name,
      'completed': this.todo.completed,
      'editing': this.todo.editing,
      'important': this.todo.important,
      'description': this.todo.description,
      'beforeEditCache': '',
    }
  },
  watch: {
    checkAll() {
      this.completed = this.checkAll ? true : this.todo.completed
    },
    importantTodo() {
      this.important = this.importantTodo ? true : this.importantTodo
    },
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  computed: {},
  methods: {
    fetchData() {
      this.$store.dispatch('retrieveTodos')
    },
    removeTodo(id) {
      this.$store.dispatch('deleteTodo', id)
    },
    editTodo() {
      this.beforeEditCache = this.title
      this.editing = true
    },
    doneEdit() {
      if (this.title.trim() == '') {
        this.title = this.beforeEditCache
      }
      this.editing = false
      this.$store.dispatch('updateTodo', {
        'id': this.id,
        'title': this.title,
        'completed': this.completed,
        'editing': this.editing,
        'important': this.important,
        'description': this.description,
      })
    },
    cancelEdit() {
      this.title = this.beforeEditCache
      this.editing = false
    },
    importantToggle() {
      if (this.important == 0 || null) {
        this.important = true
      } else {
        this.important = false
      }
      axios.put('/item/' + this.id, {
        item: {
          "name": this.title,
          "important": this.important,
          "completed": this.todo.completed,
          'description': this.todo.description,

        }
      }).then(this.fetchData())
      this.$store.dispatch('showImportant')
    },
  }
}
</script>
