<template>
  <div class="todo-item">
    <div class="todo-item-left">
      <input type="checkbox" v-model="completed" @change="doneEditGroupTodo">
      <!--      <md-checkbox   v-model="completed"  @change="doneEdit"></md-checkbox>-->
      <div v-if="!editing" @dblclick="editTodo" class="todo-item-label" :class="{ completed : completed }"
           style="font-size: 20px">
        {{ title }}
      </div>
      <input v-else class="todo-item-edit" type="text" v-model="title"
             @blur="doneEditGroupTodo" @keyup.enter="doneEditGroupTodo" @keyup.esc="cancelEdit">
    </div>
    <!-- end todo-item-left -->

    <div class="remove-item">

      <md-avatar v-for="user in todo.assignees " :key="user.id" style="margin-right: 10px;z-index: 10000">
        <img  class="img" :src="`https://todolist-oncymstoua-ey.a.run.app${user.file_path}`"/>
        <md-tooltip md-direction="bottom">{{ user.name }}</md-tooltip>
      </md-avatar>

        <b-icon v-if="(this.important == 0)" icon="star" font-scale="1.5" class="important"
                @click="importantToggle()" style="margin-right: 10px"></b-icon>
        <b-icon v-else-if="(this.important == 1)" icon="star-fill" font-scale="1.5" class="important"
                @click="importantToggle()" style="margin-right: 10px"></b-icon>
        <b-icon icon="trash" font-scale="1.5" @click="removeGroupTodo(todo.id)" style="margin-right: 10px"></b-icon>
        <GroupTodoModal :todo="todo" :importantGroupTodo="important"/>
<!--      </div>-->
    </div>
    <!-- end todo-item -->
  </div>
</template>

<script>
import axios from "axios";
import GroupTodoModal from "@/components/groupTodos/GroupTodoModal";

export default {
  name: 'group-todo-item',
  components: {GroupTodoModal},
  props: {
    todo: {
      type: Object,
      required: true,
    },
    checkAll: {
      type: Boolean,
      required: true,
    },
    importantGroupTodo: {
      // type: Boolean,
    },
  },
  data() {
    return {
      'id': this.todo.id,
      'title': this.todo.name,
      'completed': this.todo.completed,
      'editing': this.todo.editing,
      'important': this.todo.important,
      'description': this.todo.description,
      'beforeEditCache': '',
      assignee: null,
      groupId: 0,
      users: [],
      assignees: [],
      showDialog: false,
      options: []
    }
  },
  computed: {
    groupTodosFiltered() {
      return this.$store.getters.groupTodosFiltered
    },
    showAssign() {
      return this.$store.getters.showAssign
    },
    showAssignDelete() {
      return this.$store.getters.showAssignDelete
    },
    groups() {
      return this.$store.getters.retrieveGroups
    },
  },
  watch: {
    checkAll() {
      this.completed = this.checkAll ? true : this.todo.completed
    },
    importantGroupTodo(){
      this.important = this.importantGroupTodo ? true : this.todo.important
    },
  },
  directives: {
    focus: {
      inserted: function (el) {
        el.focus()
      }
    }
  },
  methods: {
    showAssignForm() {
      this.$store.dispatch('showAssign')
    },
    fetchData() {
      this.groupId = this.$route.params.id
      this.$store.dispatch('retrieveGroupTodos', this.groupId)
    },
    removeGroupTodo(id) {
      this.$store.dispatch('deleteGroupTodo', id)
    },
    editTodo() {
      this.beforeEditCache = this.title
      this.editing = true
    },
    doneEditGroupTodo() {
      if (this.title.trim() == '') {
        this.title = this.beforeEditCache
      }
      this.editing = false
      this.$store.dispatch('updateGroupTodo', {
        'id': this.id,
        'title': this.title,
        'completed': this.completed,
        'editing': this.editing,
        'important': this.important,
        'description': this.description
      })
          // .then(this.$store.dispatch('retrieveGroupTodos', this.groupId))
    },
    cancelEdit() {
      this.title = this.beforeEditCache
      this.editing = false
    },
    importantToggle() {
      if (this.important == 0 || null) {
        this.important = true
      } else {
        this.important = false
      }
      axios.put('/group/item/' + this.id, {
        item: {
          "name": this.title,
          "important": this.important,
          "completed": this.todo.completed,
          'description': this.todo.description,
        }
      }).then(this.fetchData())
    },
  },

  created() {
    this.groupId = this.$route.params.id
    // this.findGroupUsers()
    // this.$store.dispatch('findGroupUsers')
  },
}
</script>

