import App from "@/App";
import Groups from "@/components/marketing/Groups";
import VueRouter from 'vue-router';
import Vue from "vue";
import Login from './components/auth/Login';
import Register from './components/auth/Register';
import Logout from "@/components/auth/Logout";
import GroupTodos from '@/components/marketing/GroupTodos';
import Profile from '@/components/marketing/Profile'
Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'home',
        component: App,
        meta: {
            requiresAuth: true,
        }
    },
    {
        path: '/profile',
        name: 'profile',
        component: Profile,
        meta: {
            requiresAuth: true,
        }
    },
    {
        path: '/groups',
        name: 'groups',
        component: Groups,
        meta: {
            requiresAuth: true,
        }
    },
    {
        path: '/login',
        name: 'login',
        component: Login,
        meta: {
            requiresVisitor: true,
        }
    },
    {
        path: '/register',
        name: 'register',
        component: Register,
        meta: {
            requiresAuth: true,
        }
    },
    {
        path: '/logout',
        name: 'logout',
        component: Logout
    },
    {
        path: '/group/:id',
        name: 'media',
        component: GroupTodos,
        props: true,
    }
]
export default routes