<template>
  <b-container class="bv-example-row" style="display:block;">
    <md-button class="md-primary md-raised" @click="showDialog = true">Show Dialog</md-button>

    <md-dialog :md-active.sync="showDialog">

      <div style="padding: 45px;margin: auto;width: 700px;height: 80%">
        <md-dialog-title style="display: flex; justify-content: space-between">
          Groups
        </md-dialog-title>

        <md-field>
          <label>Add new group</label>
          <md-input v-model="groupName" @keyup.enter="addNewGroup"/>
        </md-field>
        <md-button class="md-primary" @click="addNewGroup(groupName)" style="text-align: center">Add new group
        </md-button>
        <br/>
        <label>Add user to the group</label>

        <v-select @search="fetchUsers" :options="options" label="name" v-model="user">
          <template v-slot:spinner="{ loading }">
            <div v-show="loading">Loading...</div>
          </template>
          <template slot="option" slot-scope="option">
            <md-avatar style="margin-right: 5px">
              <img :src="`https://todolist-oncymstoua-ey.a.run.app${option.file_path}`" alt="avatar"/>
            </md-avatar>
            {{ option.name }}
          </template>

          <template slot="selected-option" slot-scope="option">
            <md-avatar style="margin-right: 5px">
              <img :src="`https://todolist-oncymstoua-ey.a.run.app${option.file_path}`" alt="avatar"/>
            </md-avatar>
            {{ option.name }}
          </template>
        </v-select>


        <br/>
        <v-select @search="fetchGroups" :options="allGroups" label="name" v-model="groupen">
<!--          <template v-slot:spinner="{ loading }">-->
<!--            <div v-show="loading">Loading...</div>-->
<!--          </template>-->

          <template slot="options">
            <div style="display:flex;align-items: center">
              <!--                <img :src='@/assets/avatar/jpg'/>-->
              {{ options.name }}
            </div>

          </template>
        </v-select>

        <md-button class="md-primary" @click="groupAssign(groupen.id, user.id)" style="text-align: center">Add user
        </md-button>
        <md-button class="md-primary" @click="deleteGroup(groupen.id)" style="text-align: center">Delete group
        </md-button>

        <md-dialog-actions>
          <md-button class="md-primary" @click="showDialog = false">Close</md-button>
        </md-dialog-actions>
      </div>
    </md-dialog>


    <b-row cols="3" cols-sm="3" cols-md="3" cols-lg="3">
      <b-col v-for="group in groups" v-bind:key="group.id" style="padding: 10px">
        <router-link :to="{ path: 'group/' + group.id }">
          <b-card
              img-src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRi5iJL5J1U5gYguOQzEcJQHpjK05sC81v2Hg&usqp=CAU"
              img-alt="Card image" img-top>
            <b-card-text style="text-align: center">
              <div class="group-details">
                <div>
                  <div style="margin-left: 10px">
                    <md-avatar v-for="user in group.users" v-bind:key="user.name" style="margin-left: -15px">
                      <img  class="img" :src="`https://todolist-oncymstoua-ey.a.run.app${user.file_path}`"/>
                      <md-tooltip md-direction="bottom">{{ user.name }}</md-tooltip>
                    </md-avatar>
                  </div>
                </div>
                <h4 style="color: black">{{ group.name }}</h4>
              </div>
            </b-card-text>
          </b-card>
        </router-link>

      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      showDialog: false,
      options: [],
      groupen: null,
      user: null,
      groupName: '',
      allGroups: [],
    }
  },
  methods: {
    fetchData(){
      // this.$store.dispatch('retrieveGroups')
      this.fetchGroups()
    },
    fetchUsers(search, loading) {
      if (search.length == 5) {
        loading(true);
        axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
        axios.get('/users')
            .then(response => {
              console.log(response)
              this.options = response.data
            }).then(loading(false))
            .catch(error => {
              console.log(error)
            })
      }
    },
    groupAssign(groupId, userId) {
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
      axios.post('/group/' + groupId + '/' + userId)
          .then(response => {
            console.log(response)
            // this.options = response
            this.fetchData()
          }).catch(error => {
        console.log(error)
      })
    },
    addNewGroup(groupName) {
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
      axios.post('/group/', {
        item: {
          "name": groupName
        }
      })
          .then(this.fetchData()
          ).catch(error => {
        console.log(error)
      })
    },
    deleteGroup(id) {
      // this.$store.dispatch('deleteGroup',id )
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
      axios.delete('/group/' + id)
          // eslint-disable-next-line no-unused-vars
          .then(response =>{
            this.fetchData()
          })
          .catch(error => {
            console.log(error)
          })
    },
    fetchGroups() {
      axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
      axios.get('/Allgroups').then(response => {
        console.log(response.data)
        this.$store.state.groups = response.data
        this.allGroups = response.data
      })
    }
  },
  computed: {
    groups() {
      return this.$store.state.groups
    },
  },
  created() {
    this.fetchData()
  }
}
</script>
<style>
.group-details {
  display: flex;
  justify-content: space-between;
}
</style>