import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)
axios.defaults.baseURL = 'https://todolist-oncymstoua-ey.a.run.app/api'
export const store = new Vuex.Store({
    state: {
        token: localStorage.getItem('access_token') || null,
        filter: 'all',
        groupFilter: 'all',
        todos: [],
        groups: [],
        groupTodos: [],
        showUpdate: false,
        showImportant:false,
        showAssign: false,
        showAssignDelete: false,
        showRegister: false,
        userName: '',
        groupName: '',
        groupUsers: [],
        assignees: [],
        allGroupTodos:[],
        user:{},
        userPhoto:null,
    },
    getters: {
        loggedIn(state) {
            return state.token !== null
        },
        remaining(state) {
            return state.todos.filter(todo => !todo.completed).length
        },
        anyRemaining(state, getters) {
            return getters.remaining != 0
        },
        important(state){
            return state.todos.filter(todo=>!todo.important)
        },
        groupRemaining(state) {
            return state.groupTodos.filter(todo => !todo.completed).length
        },
        anyGroupRemaining(state, getters) {
            return getters.groupRemaining != 0
        },
        todosFiltered(state) {
            if (state.filter == 'all') {
                return state.todos
            } else if (state.filter == 'active') {
                return state.todos.filter(todo => !todo.completed)
            } else if (state.filter == 'completed') {
                return state.todos.filter(todo => todo.completed)
            } else if (state.filter == 'important') {
                return state.todos.filter(todo => todo.important)
            }
            return state.todos
        },
        groupTodosFiltered(state) {
            if (state.groupFilter == 'all') {
                return state.groupTodos
            } else if (state.groupFilter == 'active') {
                return state.groupTodos.filter(todo => !todo.completed)
            } else if (state.groupFilter == 'completed') {
                return state.groupTodos.filter(todo => todo.completed)
            } else if (state.groupFilter == 'important') {
                return state.groupTodos.filter(todo => todo.important)
            }
            return state.groupTodos
        },
        allGroupTodos(state){
            return state.allGroupTodos
        },
        showClearCompletedButton(state) {
            return state.todos.filter(todo => todo.completed).length > 0
        },
        showUpdate(state) {
            return state.showUpdate
        },
        showImportant(state){
            return state.showImportant
        },
        showAssign(state) {
            return state.showAssign
        },
        showAssignDelete(state){
            return state.showAssignDelete
        },
        showRegister(state) {
            return state.showRegister
        },
        showUser(state) {
            return state.userName
        },
        retrieveUser(state){
            return state.user
        },
        showGroupName(state) {
            return state.groupName
        },
        retrieveGroups(state) {
            return state.groups
        },
        retrieveGroupUsers(state) {
            return state.groupUsers
        },
        retrieveAssignees(state) {
            return state.assignees
        },
        retrieveGroupUserOne() {
            return this.state.group
        }
    },
    mutations: {
        retrieveUserName(state, userName) {
            state.userName = userName
        },
        retrieveUser(state,user){
            state.user = user
        },
        retrieveUserPhoto(state, userPhoto){
            state.userPhoto = userPhoto
        },
        addTodo(state, todo) {
            state.todos.push({
                id: todo.id,
                title: todo.title,
                completed: false,
                editing: false,
            })
        },
        updateTodo(state, todo) {
            const index = state.todos.findIndex(item => item.id == todo.id)
            state.todos.splice(index, 1, {
                'id': todo.id,
                'name': todo.name,
                'completed': todo.completed,
                'editing': todo.editing,
                'description':todo.description,
            })
        },
        deleteTodo(state, id) {
            const index = state.todos.findIndex(item => item.id == id)
            state.todos.splice(index, 1)
        },
        deleteGroupTodo(state, id) {
            const index = state.groupTodos.findIndex(item => item.id == id)
            state.groupTodos.splice(index, 1)
        },
        deleteGroup(state,id){
            const index = state.groups.findIndex(item => item.id == id)
            state.groups.splice(index, 1)
        },
        checkAll(state, checked) {
            state.todos.forEach(todo => (todo.completed = checked))
        },
        checkAllGroupTodos(state, checked) {
            state.groupTodos.forEach(todo => (todo.completed = checked))
        },
        updateFilter(state, filter) {
            state.filter = filter
        },
        updateGroupFilter(state, groupFilter) {
            state.groupFilter = groupFilter
        },
        clearCompleted(state) {
            state.todos = state.todos.filter(todo => !todo.completed)
        },
        retrieveTodos(state, todos) {
            state.todos = todos
        },
        retrieveTodo(state, todo) {
            const index = state.todos.findIndex(item => item.id == todo.id)
            console.log(todo)
            console.log(state.todos[index])
            state.todos[index] = todo
        },
        retrieveGroups(state, groups) {
            // state.groups = groups
            let found = false;
            for (let i = 0; i < state.groups.length; i++) {
                if (state.groups[i].name == groups.name) {
                    found = true;
                }
            }
            if (!found) {
                state.groups.push(groups);
            }
        },
        retrieveGroupName(state, groupName) {
            state.groupName = groupName
        },
        retrieveGroupTodos(state, groupTodos) {
            state.groupTodos = groupTodos
        },
        retrieveAllGroupTodos(state, allGroupTodos) {
            state.allGroupTodos = allGroupTodos
        },
        retrieveToken(state, token) {
            state.token = token
        },
        destroyToken(state) {
            state.token = null
        },
        clearTodos(state) {
            state.todos = []
        },
        showUpdate(state, showUpdate) {
            state.showUpdate = showUpdate
        },
        showImportant(state, showImportant) {
            state.showImportant = showImportant
        },
        showAssign(state, showAssign) {
            state.showAssign = showAssign
        },
        showAssignDelete(state, showAssignDelete) {
            state.showAssignDelete = showAssignDelete
        },
        showRegister(state, showRegister) {
            state.showRegister = showRegister
        },
        retrieveGroupUsers(state, groupUsers) {
                state.groupUsers = groupUsers;
        },
        updateGroupTodo(state, todo) {
            const index = state.groupTodos.findIndex(item => item.id == todo.id)
            // state.groupTodos.splice(index, 1, {
            state.groupTodos[index].completed = todo.completed
            state.groupTodos[index].name = todo.name
            state.groupTodos[index].editing = todo.editing
            state.groupTodos[index].description = todo.description
            // 'id': todo.id,
            //     'name': todo.name,
            //     'completed': todo.completed,
            //     'editing': todo.editing,
            //     'description':todo.description
            // })
        },
        getAssigneeName(state, assignee) {
            let found = false;
            for (let i = 0; i < state.assignees.length; i++) {
                if (state.assignees[i].id == assignee.id) {
                    found = true;
                }
            }
            if (!found) {
                state.assignees.push(assignee);
            }
        },
    },
    actions: {
        retrieveUserName(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/user')
                .then(response => {
                    context.commit('retrieveUserName', response.data.name)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        retrieveUser(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/user')
                .then(response => {
                    console.log(response)
                    context.commit('retrieveUser', response.data)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        retrieveUserPhoto(context){
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/user/image/receive')
                .then(response => {
                    context.commit('retrieveUserPhoto',response.data)
                })
                .catch(error => {
                console.log(error)
            })

        },

        clearTodos(context) {
            context.commit('clearTodos')
        },
        register(context, data) {
            return new Promise((resolve, reject) => {
                axios.post('/register', {
                    name: data.name,
                    email: data.email,
                    password: data.password,
                })
                    .then(response => {
                        resolve(response)
                    })
                    .catch(error => {
                        reject(error)
                    })
            })
        },
        destroyToken(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token

            if (context.getters.loggedIn) {
                return new Promise((resolve, reject) => {
                    axios.post('/logout')
                        .then(response => {
                            localStorage.removeItem('access_token')
                            context.commit('destroyToken')
                            resolve(response)
                        })
                        .catch(error => {
                            localStorage.removeItem('access_token')
                            context.commit('destroyToken')
                            reject(error)
                        })
                })
            }
        },
        retrieveToken(context, credentials) {
            return new Promise((resolve, reject) => {
                axios.post('/login', {
                    username: credentials.username,
                    password: credentials.password,
                })
                    .then(response => {
                        const token = response.data.access_token

                        localStorage.setItem('access_token', token)
                        context.commit('retrieveToken', token)
                        resolve(response)
                        console.log(response);
                    })
                    .catch(error => {
                        console.log(error)
                        reject(error)
                    })
            })
        },
        retrieveTodos(context) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/items')
                .then(response => {
                    console.log(response)
                    context.commit('retrieveTodos', response.data)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        retrieveTodo(context, id) {
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/item/' + id)
                .then(response => {
                    console.log(response)
                    context.commit('retrieveTodo', response.data)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        retrieveGroups(context, id) {
                axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
                axios.get('/groups')
                    .then(response => {
                        console.log(response)
                        for (let i = 0; i < response.data.length; i++) {
                            console.log(response.data[i].users)
                            context.commit('retrieveGroups', response.data[i])
                            // context.commit('retrieveGroupUsers', response.data[i].users)
                        }
                        context.commit('retrieveGroupName', response.data.find(x => x.id == id).name)
                    })
                    .catch(error => {
                        console.log(error)
                    })
        },
        retrieveGroupTodos(context, id) {
            console.log(id)
            axios.defaults.headers.common['Authorization'] = 'Bearer ' + context.state.token
            axios.get('/group/' + id)
                .then(response => {
                    console.log(response)
                    context.commit('retrieveGroupTodos', response.data)
                })
                .catch(error => {
                    console.log(error)
                })

        },
        // retrieveAllGroupTodos(context) {
        //
        //         .catch(error => {
        //             console.log(error)
        //         })
        // },
        addTodo() {
        },
        updateTodo(context, todo) {
            axios.put('/item/' + todo.id, {
                item: {
                    "name": todo.title,
                    'important': todo.important,
                    "completed": todo.completed,
                    'description':todo.description,
                }
            }).then(response => {
                context.commit('updateTodo', response.data)
            })
        },
        deleteTodo(context, id) {
            axios.delete('/item/' + id)
                // eslint-disable-next-line no-unused-vars
                .then(response => {
                    context.commit('deleteTodo', id)
                })
                .catch(error => {
                    console.log(error)
                })
        },
        deleteGroupTodo(context, id) {
            axios.delete('group/item/' + id)
                // eslint-disable-next-line no-unused-vars
                .then(response => {
                    context.commit('deleteGroupTodo', id)
                })
        },
        // deleteGroup(context, id){
        //     axios.defaults.headers.common['Authorization'] = 'Bearer ' + this.$store.state.token
        //     axios.delete('/group/' + id)
        //         // eslint-disable-next-line no-unused-vars
        //         .then(response => {
        //             context.commit('deleteGroup', id)
        //         })
        //         .catch(error => {
        //             console.log(error)
        //         })
        // },
        checkAll(context, checked) {
            axios.patch('/todosCheckAll', {
                completed: checked,
            })
                //eslint-disable-next-line no-unused-vars
                .then(response => {
                    context.commit('checkAll', checked)
                })
        },
        checkAllGroupTodos(context, checked, id) {
            axios.patch('/group/' + id + '/todosCheckAll', {
                completed: checked,
            })
                //eslint-disable-next-line no-unused-vars
                .then(response => {
                    context.commit('checkAllGroupTodos', checked)
                })
        },
        updateFilter(context, filter) {
            context.commit('updateFilter', filter)
        },
        updateGroupFilter(context, groupFilter) {
            context.commit('updateGroupFilter', groupFilter)
        },
        clearCompleted(context) {
            context.state.todos.filter(todo => todo.completed).forEach(todo => {
                axios.delete('item/' + todo.id)
            })
        },
        showUpdate(context) {
            this.showUpdate = !this.showUpdate
            context.commit('showUpdate', this.showUpdate)
        },
        showImportant(context){
            this.showImportant = !this.showImportant
            context.commit('showImportant', this.showImportant)
        },
        showAssign(context) {
            this.showAssign = !this.showAssign
            context.commit('showAssign', this.showAssign)
        },
        showAssignDelete(context) {
            this.showAssignDelete = !this.showAssignDelete
            context.commit('showAssignDelete', this.showAssignDelete)
        },
        showRegister(context) {
            this.showRegister = !this.showRegister
            context.commit('showRegister', this.showRegister)
        },
        //group todos
        updateGroupTodo(context, todo) {
            axios.put('group/item/' + todo.id, {
                item: {
                    "name": todo.title,
                    'important': todo.important,
                    "completed": todo.completed,
                    'description':todo.description,
                }
            })
                .then(response => {
                context.commit('updateGroupTodo', response.data)
            })
        },
         getAssigneeName(context) {
            console.log(context.state.groupTodos.length)
            for (let i = 0; i < context.state.groupTodos.length; i++) {
                if (context.state.groupTodos[i].assignee != null) {
                    axios.get('/group/item/assigneeName/' + context.state.groupTodos[i].assignee)
                        .then(response => {
                            console.log(response.data)
                            context.commit('getAssigneeName', response.data)
                        })
                }
            }
        },
        findGroupUsers(context, groupId){
            context.state.groups.forEach((group) => {
                if(group.id == groupId){
                    this.users = group.users
                    context.commit('retrieveGroupUsers', group.users)
                }
            })
    }
 }
})
