<template>
  <div>
    <div>
      <b-button style="margin-right: 10px" :class="{ active: filter == 'all' }" @click="changeFilter('all')" pill
                variant="outline-secondary">Alle
      </b-button>
      <b-button style="margin-right: 10px" :class="{ active: filter == 'active' }" @click="changeFilter('active')" pill
                variant="outline-secondary">Aktiv
      </b-button>
      <b-button style="margin-right: 10px" :class="{ active: filter == 'completed' }" @click="changeFilter('completed')"
                pill
                variant="outline-secondary">Fertig
      </b-button>
      <b-button :class="{ active: filter == 'important' }" @click="changeFilter('important')" pill
                variant="outline-secondary">Wichtig
      </b-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'todo-filtered',
  computed: {
    filter() {
      return this.$store.state.filter
    }
  },
  methods: {
    changeFilter(filter) {
      this.$store.dispatch('updateFilter', filter)
        this.$store.dispatch('retrieveTodos')
    }
  }
}
</script>
